<template>
    <div class="login-page">
        <div class="shade-bg">
          <div class="inner-content">
            <!-- 登录信息 -->
            <div class="member-login">
            <!-- 血战图标 -->
            <div class="head-img">
              <img src="../assets/xzdd_icon.png" alt="">
            </div>
              <div class="list-item">
                <input type="tel" v-model="login.user_name" @input="filter('user_name')" class='add-ipt' maxlength="11" placeholder="手机号" />
              </div>
              <div class="list-item">
                <input type="password" v-model="login.password" @input="filter('password')" class='add-ipt' maxlength="18" placeholder="密码" />
              </div>
              <div class="sure-btn" @click="_login">登录</div>
              <!-- <a href="" class="forget">忘记密码</a> -->
            </div>
          </div>
        </div>
        <!-- 提示框 -->
        <tips :tipsStr="tipsStr" ref="tips"></tips>
        <!-- 等待加载 -->
        <loading ref="loading"></loading>
    </div>
</template>

<script>
    import tips from 'components/plugins/tips';
    import loading from 'components/plugins/loading';

    export default {
      data () {
        return {
          // 小弹框里面的字段存储的地方
          tipsStr: '',
          login: {
            user_name: '',
            password: ''
          }
        };
      },
      methods: {
        _login () {
          // 验证手机号码
          if (!/^1[34578]\d{9}$/.test(this.login.user_name)) {
            this._showTip('手机号码格式错误');
            return;
          }
          // 验证密码是否输入
          if (!/^\w{6,18}$/.test(this.login.password)) {
            this._showTip('密码不能少于6位数');
            return;
          }
          // 通过验证进行提交
          this.$refs.loading.showLoad();
          setTimeout(() => {
            this.$refs.loading.hideLoad();
            this.$router.replace({ name: 'address' });
          }, 1500);
        },
        // 过滤空格
        filter (str) {
          this.login[str] = this.login[str].replace(/\s/g, '');
        },
        /**
        * [_showTip description] 显示提示框
        * @param  {[type]} str [传到提示框的字段]
        * @return {[type]}     [description]
        */
        _showTip (str) {
          this.tipsStr = str;
          this.$refs.tips.showTips();
        }
      },
      components: {
        loading,
        tips
      }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import '../common/stylus/mixin.styl';

  .login-page
    position: absolute
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: url(../assets/xz_bg.jpg) no-repeat
    background-size: 100% 100%
    .shade-bg
      position: absolute
      top: 0
      left: 0
      bottom: 0
      right: 0
      padding-top: 1.5rem
      width: 16rem
      height: 10rem
      margin: auto
      background: url(../assets/shade_bg.png) no-repeat
      background-size: 100% 100%
      border-radius: 15px 
      .login-btn
        display: block
        padding: 10px 0
        width: 100%
        margin-top: 15px
        border-radius: 25px
        text-align: center
        background: #ff991d
        color: #fff
      .forget
        float: right
        margin: 18px 10px 0 0
        color: #666
      .member-login
        position: relative
        padding: 0 20px
        .head-img
          position: absolute
          top: -8.9rem
          left: 0
          width: 100%
          img
            display: block
            width: 14rem
            height: auto
            margin: 1rem auto
        .list-item
          position: relative
          .add-ipt
            display: inline-block
            margin: 0.5rem 0
            padding: 0.5rem 7%
            width: 86%
            background: rgba(26, 122, 183, 0.7)
            border-radius: 25px
            font-size: 0.8rem
            color: #fff
        .sure-btn
          width: 6.6rem
          height: 2.12rem
          margin: auto
          text-align: center
          line-height: 2.1rem
          margin-top: 0.6rem
          background: url(../assets/sure_btn_icon.png) no-repeat
          background-size: 100% 100%
          font-size: 0.9rem
          color: #fff
          &:active
            transform: scale(1.2, 1.2)
</style>



